/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

.header__img {
  width: 25rem;
  height: 6.3rem;
  margin: 9.5rem 3.5rem 3.6rem 3.5rem; }
  @media all and (min-width: 768px) {
    .header__img {
      width: 42rem;
      height: 10.5rem; } }

.main-landing {
  background-color: #f5f5f5;
  height: 95vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }

.card-creation {
  display: flex;
  flex-direction: column;
  align-items: center; }
  .card-creation__title::first-letter {
    text-transform: uppercase; }
  .card-creation__title {
    color: #e17334;
    font-family: "Merriweather", serif;
    font-size: 1.92rem;
    font-weight: 900;
    line-height: normal; }
    @media all and (min-width: 768px) {
      .card-creation__title {
        font-size: 2.4rem;
        padding-top: 3.1rem; } }
  .card-creation__paragraph::first-letter {
    text-transform: uppercase; }
  .card-creation__paragraph {
    width: 26.5rem;
    height: 3.84rem;
    color: #114e4e;
    font-family: "Open Sans", sans-serif;
    font-size: 1.28rem;
    line-height: normal;
    text-align: center; }
    @media all and (min-width: 768px) {
      .card-creation__paragraph {
        width: 33rem;
        height: 4.8rem;
        font-size: 1.6rem;
        padding-bottom: 2.7rem; } }
  .card-creation__div {
    width: 26.8rem;
    height: 7.38rem;
    border: solid 0.16rem #e17334;
    border-radius: 0.4rem;
    margin: 1.78rem 0.2rem 0 0.2rem;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center; }
    @media all and (min-width: 768px) {
      .card-creation__div {
        width: 32.5rem;
        height: 8.6rem;
        margin-bottom: 8rem; } }
    .card-creation__div--item {
      width: 6rem;
      height: 4.8rem;
      font-size: 1.28rem;
      text-align: center;
      display: flex;
      flex-direction: column-reverse;
      justify-content: center;
      color: #54585a;
      font-family: "Open Sans", sans-serif; }
      @media all and (min-width: 768px) {
        .card-creation__div--item {
          width: 7.5rem;
          height: 6.1rem;
          font-size: 1.6rem;
          justify-content: center;
          align-items: center; } }
  .card-creation__button {
    padding: 1.2rem 2.2rem;
    margin: 5rem 0 3rem 0;
    background-color: #438792;
    border: solid #438792;
    border-radius: 2.1rem;
    text-transform: uppercase;
    color: #fff;
    font-family: "Open Sans", sans-serif;
    text-decoration: none;
    font-size: 1.6rem;
    font-weight: 600;
    cursor: pointer; }
    @media all and (min-width: 768px) {
      .card-creation__button {
        margin: 0rem 0 3rem 0; } }

.fa-object-ungroup, .fa-keyboard, .fa-share-nodes {
  color: #e17334;
  font-size: 2.24rem;
  width: 6rem; }

.footer {
  width: 100%;
  height: 8vh;
  background-color: #fff;
  border-top: solid 0.1rem #d8d8d8;
  display: flex;
  flex-direction: column;
  align-items: center; }
  @media all and (min-width: 768px) {
    .footer {
      display: flex;
      flex-direction: row;
      justify-content: space-between; } }
  .footer__text::first-letter {
    text-transform: uppercase; }
  .footer__text {
    font-family: "Open Sans", sans-serif;
    font-size: 1.4rem;
    color: #6b6b6b;
    opacity: 0.5;
    margin: 3rem 0 0 0; }
    @media all and (min-width: 768px) {
      .footer__text {
        padding-left: 4rem; } }
  .footer__logo {
    width: 10.3rem;
    height: 4rem;
    margin: 1.2rem 4.7rem 2rem 4.8rem; }

html {
  font-size: 62.5%; }

.footer {
  height: 15vh; }
